.c-frame {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    //transform: translateY(calc(-50% + 50px));

    &__line {
        display: inline-block;
        background-color: $white;
        position: absolute;

        &--left {
            top: var(--frame-offset);
            bottom: var(--frame-offset);
            left: var(--frame-offset);
            width: 3px;
            transform-origin: bottom;
            transform: scaleY(0);
        }

        &--right {
            top: var(--frame-offset);
            bottom: var(--frame-offset);
            right: var(--frame-offset);
            width: 3px;
            transform-origin: bottom;
            transform: scaleY(0);
        }

        &--top-left {
            top: var(--frame-offset);
            left: var(--frame-offset);
            right: 50%;
            height: 3px;
            transform-origin: left;
            transform: scaleX(0);
        }

        &--top-right {
            top: var(--frame-offset);
            right: var(--frame-offset);
            left: 50%;
            height: 3px;
            transform-origin: right;
            transform: scaleX(0);
        }

        &--bottom {
            bottom: var(--frame-offset);
            left: var(--frame-offset);
            right: var(--frame-offset);
            height: 3px;
            transform-origin: center;
            transform: scaleX(0);
        }
    }
}
